for (let i = 0; i < 200; i = i + 15) {

   let sphere = new THREE.SphereGeometry(i / 7, 64, 128)
   let toon = new THREE.MeshToonMaterial()

   const mesh2 = new THREE.Mesh(sphere, toon)
   mesh2.position.x = Math.random() * 200;
   mesh2.position.y = Math.random() * 200;
   mesh2.position.z = Math.random() * 200;

   var sounds = new THREE.PositionalAudio(listener4);
   soundlist.push(sounds)
   Tone.setContext(sounds.context);

   let oscillators = new Tone.Oscillator(Math.abs(i * root), "triangle").toDestination();

   oscs.push(oscillators)
   sounds.setMaxDistance(400)
   sounds.setRefDistance(1000)
   sounds.setNodeSource(oscillators);
   sounds.setRolloffFactor(0.01)
   sounds.setDistanceModel('linear')
   sounds.setDirectionalCone(180, 230, 0.9);
   mesh2.add(sounds);
   scene.add(mesh2)
   meshes.push(mesh2)
   oscillators.volume.value = -36

   const mesh4 = new THREE.Mesh(
      new THREE.SphereGeometry(i / 60, 64, 128),
      new THREE.MeshToonMaterial()
   )
   var sound2 = new THREE.PositionalAudio(listener4);
   Tone.setContext(sound2.context);
   let oscillator2 = new Tone.Oscillator(Math.abs(i * root/2), "square").toDestination();
   osc2.push(oscillator2)
   oscillator2.volume.value = -60
   sound2.setNodeSource(oscillator2);
   sound2.setMaxDistance(800)
   sound2.setRefDistance(1000)
   sound2.setRolloffFactor(0.001)
   sound2.setDistanceModel('linear')
   sound2.setDirectionalCone(180, 230, 0.9);
   scene.add(mesh4)
   mesh4.parent = mesh2
   meshez.push(mesh4)
   mesh4.add(sound2)
}

