function checkPass() {
   let password = document.querySelector('input').value
   if (password == 'DORMANT') {
      document.getElementById('starter').remove()
      sizes.width = window.innerWidth
      sizes.height = window.innerHeight

      // Update camera
      camera.aspect = (sizes.width / sizes.height)
      camera.updateProjectionMatrix();

      camera.width = sizes * camera.aspect / -2;
      camera.height = sizes * camera.aspect / 2;

      // Update renderer
      renderer.setSize(sizes.width, sizes.height)
      renderer.setPixelRatio(Math.min(2, 2))

      for (let i = 0; i < oscs.length; i++) {
         oscs[i].start()
      }

      for (let t = 0; t < osc2.length; t++) {
         osc2[t].start()
      }
      
      pattern.start()
      pattern2.start()
      Tone.Transport.start();
      start = true
   }
}

camera.rotation.x = -4.75
