camera.zoom = 0.1

const tick = () => {
   
   let bpmcam = map(camera.zoom, 0.04, 1.2, 220, 107)
   Tone.Transport.bpm.value = bpmcam;
   let frequencyData = analyzer.getValue();
   var scale2 = Math.floor(Math.abs(frequencyData[frequencyData.length - 1])) - 112
   // console.log(scale2)
   controls.target.set(0, 0, 0.1);
   const timer = 0.0001 * Date.now();
   x += speed
   z += speed2


   if (start == false) {
      checkPass()
   }

   // console.log(pointer)

   if(pointer.x > 0.3 && pointer.x < 0.97 && pointer.y < 0.3 && pointer.y > -0.90){
      slip = true
      for (let y = 0; y < meshes.length; y++) {
         oscs[y].volume.rampTo(-200, 5)
         osc2[y].frequency.rampTo("A3", 10)
         osc2[y].volume.rampTo(-50, 5)
         kit.volume.rampTo(-200, 3)    
         synth.volume.rampTo(-200, 5)
         synth2.volume.rampTo(-200, 3) 
         scene.background = new THREE.Color(50, 0, 0)
      
      }
   } else {
         slip = false
      }
   

   raycaster.setFromCamera(pointer, camera);
   const intersects = raycaster.intersectObjects(scene.children, false)

   if (intersects.length > 0) {
      document.getElementById('column').append(drop[i].toUpperCase() + ' ')
 
      if (intersects[0].object != mesh7) {
         INTERSECTED = intersects[0].object;
         recent = INTERSECTED.children[0].source
         recent.volume.rampTo(-18, 1)
         
   
         if (recent != recentList[recentList.length -1]) {
            arp.push(recent.frequency.value / 2)
            arp2.push(recent.frequency.value / 4)
         }
         if (arp.length > 4) {
            arp.shift()
         }

         if (arp2.length > 8) {
            arp2.shift()
         }
         recentList.push(recent)
         for (let y = 0; y < meshes.length; y++) {
            let objects = meshes[y]
            if (objects == INTERSECTED) {
               document.getElementById('overlay').innerHTML = ''
               document.getElementById('overlay').append(messages[y].toUpperCase())
               
            }
         }
      }
   i++
   if(i > drop.length - 5){
      i = 0
   }
   } else {

      root = 12
      document.getElementById('column').innerHTML = ''
      for (let y = 0; y < meshes.length; y++) {
         let objects = meshes[y]
         let object2 = meshez[y]
         object2.position.x = objects.position.x + 5
         objects.updateMatrixWorld()
         object2.updateMatrixWorld()
         let distance = camera.position.distanceTo(objects.position);
         let distance2 = camera.position.distanceTo(object2.position);
         let xplace = objects.position.x + objects.position.z + objects.position.y
         let freqVol = Math.floor(map(xplace, -500, 500, 220, 880))
         let disVol = map(distance, 10, 300, 1, 8)
         let disVol2 = map(distance2, 10, 300, 1, 8)
         let expVol = Math.exp(disVol)
         let expVol2 = Math.exp(disVol2)
         let holderP = -expVol * camera.zoom * 2.3 - 50
         if(slip == false){
         oscs[y].volume.rampTo(holderP, 0.2)
         osc2[y].volume.rampTo(-expVol2 * (camera.zoom * 2.3) - 50, 0.6)
         osc2[y].frequency.rampTo(freqVol)
         kickvol = map(camera.zoom, 0.04, 1.2, -12, -24)
         synthvol = map(camera.zoom, 0.04, 1.2, -30, -16)
         kit.volume.value = kickvol
         synth.volume.value = synthvol
         synth2.volume.value = synthvol -5
         }
         // //////SIDECHAIN
      }


   }
   for (let y = 0; y < meshes.length; y++) {
      let objects = meshes[y]
      let sloop3 = new THREE.Color(255, 0, 0.2 + Math.sin(x / 16));  // white
      objects.material.emissive = sloop3
      objects.material.fog = sloop3
      objects.material.aoMapIntensity = y /10
      objects.material.emissiveIntensity = y /10
      if(slip == false){
      objects.position.x = 100 * Math.cos(timer + y);
      objects.position.y = 100 * Math.sin(timer + y * 1.1);
      objects.position.z = 100 * Math.sin(timer + y * 0.7);
      objects.rotation.y = 10 * Math.sin(timer + y * 0.7);
      objects.rotation.x = 10 * Math.sin(timer + y * 0.7);
      objects.rotation.z = 10 * Math.sin(timer + y * 0.7);
      }
      soundlist[y].setDirectionalCone(40 * Math.cos(timer + y), 40 * Math.sin(timer + y * 1.1), 0.9)

   }
   if (x > 16.5 || x < -1.2) {
      speed = speed * -1
   }

   if (z > 30 || x < 10) {
      speed2 = speed2 * -1
   }

   // console.log(scene.background)

   sloop2 = new THREE.Color(0.2 + Math.sin(x / 40), 0.2 + Math.sin(x / 24), 0.2 + Math.sin(x / 16));  // white
   sloop1 = new THREE.Color(0.2 + Math.sin(x / 20), 0.2 + Math.sin(x / 12), 0.2 + Math.sin(x / 8));  // white
   var light1 = new THREE.Color(0.2 + Math.sin(x / 2), 0, 0.2 + Math.sin(x));
   color = new THREE.Color(0.15 + Math.sin(x / 10), 0.2 + Math.sin(x / 8), 0.2 + Math.sin(x / 30));
   light4 = new THREE.Color(0.2 + Math.sin(x / 5), 0.2 + Math.sin(x / 5), 0.2 + Math.sin(x / 2));
   const near = 10 / camera.zoom
   const far = 15 * camera.zoom
   scene.fog = new THREE.Fog(light1, near, far);
   if(slip == false){
   scene.background = new THREE.Color(0.2 + Math.sin(x + (camera.rotation.x * 2)), 0.2 + Math.sin(x + (camera.rotation.y * 2)), 0.2 + Math.sin(x + (camera.rotation.z * 2)));
   }
   controls.update()
   camera.updateMatrixWorld();
   scene.updateMatrixWorld();

   window.requestAnimationFrame(tick)


}

